.app__chef-content{
   display: flex; 
   flex-direction: column;
   width: 100%;
   margin-top: 5rem;
}
.app__chef-content_quote{
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
}
.app__chef-content_quote img{
   width: 47px; 
   height: 40px;
   margin: 0 1rem 1rem 0;
}
.app__chef-sign{
    width: 100%;
    margin-top: 3rem;
}
.app__chef-sign p:first-child{
    font-family: var(--font-base);
    font-weight: 400;
    font-size: 32px;
    color:var(--color-golden);
    line-height: 41px;
    letter-spacing: 0.4em;
    text-transform: capitalize;
}
.app__chef-sign img{
    width: 250px;
    margin-top: 3rem;
}
.app__wrapper_info{
    color: rgb(218, 203, 155);
}

@media screen and (max-width:2000px) {
    .app__chef-sign img{
        width: 370px;
    }
}
@media screen and (max-width:450px) {
    .app__chef-sign img{
        width: 80%px;
    }
}
@media screen and (max-width:2000px) {
    .app__chef-sign img{
        width: 370px;
    }
}